<template>
  <v-container
    fluid
  >
    <v-row>
      <v-col
        cols="12"
        md="4"
      >
        <v-card
          :loading="loading"
          class="mx-auto"
        >
          <v-img
            height="250"
            :src="src"
          >
            <template v-slot:placeholder>
              <v-row
                class="fill-height ma-0"
                align="center"
                justify="center"
              >
                <v-progress-circular
                  indeterminate
                  color="grey lighten-5"
                />
              </v-row>
            </template>
          </v-img>
          <v-card-title class="d-flex justify-space-between">
            <div class="font-weight-bold">
              {{ servantData.basic.name }}
            </div>
            <div>{{ servantData.basic.password }}</div>
          </v-card-title>
          <v-card-text>
            <div class="font-weight-bold">
              {{ servantData.basic.managedSection }}
            </div>
          </v-card-text>
          <v-divider class="mx-2"/>
          <v-card-title class="subtitle-2">
            {{ $t('本日总结') }}
          </v-card-title>
          <v-card-text>
            <v-list
              shaped
              dense
            >
              <v-list-item-group
                v-model="currentIndex"
                color="primary"
              >
                <v-list-item
                  v-for="(item, i) in infoList"
                  :key="i"
                >
                  <v-list-item-content>
                    <v-list-item-title v-text="item.text"/>
                  </v-list-item-content>
                </v-list-item>
              </v-list-item-group>
            </v-list>
            <v-menu
              ref="menu"
              v-model="menu"
              :close-on-content-click="false"
              :return-value.sync="dates"
              offset-y
            >
              <template v-slot:activator="{ on }">
                <v-text-field
                  v-model="dates"
                  hide-details
                  :label="$t('日期筛选')"
                  prepend-icon="mdi-calendar"
                  single-line
                  v-on="on"
                />
              </template>
              <v-date-picker
                v-model="dates"
                no-title
                range
                scrollable
                locale="de"
              >
                <v-spacer/>
                <v-btn
                  text
                  color="primary"
                  @click="menu = false"
                >
                  {{ $t('Cancel') }}
                </v-btn>
                <v-btn
                  text
                  color="primary"
                  @click="$refs.menu.save(dates)"
                >
                  {{ $t('OK') }}
                </v-btn>
              </v-date-picker>
            </v-menu>
          </v-card-text>
          <v-card-actions>
            <v-row justify="center">
              <v-btn
                small
                color="primary"
                @click="printSummary(id)"
              >
                {{ $t('打印本日总结') }}
              </v-btn>
            </v-row>
          </v-card-actions>
        </v-card>
      </v-col>
      <v-col
        cols="12"
        md="8"
      >
        <v-container
          id="data-tables"
          class="fill-height align-start"
          fluid
          tag="section"
        >
          <v-container
            fluid
          >
            <base-material-card
              class="px-5 py-3 "
              color="indigo"
              icon="mdi-vuetify"
              inline
            >
              <template v-slot:after-heading>
                <div class="display-2 font-weight-light">
                  {{ $t('本日账单') }}
                </div>
              </template>
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                class="ml-auto"
                hide-details
                :label="$t('Search')"
                single-line
                style="max-width: 250px;"
              />
              <v-divider class="mt-3"/>
              <v-data-table
                :fixed-header="true"
                :headers="headers"
                :items="desserts"
                :items-per-page="10"
                :loading="loading"
                :search.sync="search"
                multi-sort
              >
                <template v-slot:item.paymentStatus="{ item }">
                  <v-checkbox
                    :readonly="true"
                    :input-value="item.paymentStatus==='1'"
                    disabled
                  />
                </template>
                <template v-slot:item.action="{ item }">
                  <v-icon
                    class="mr-2"
                    small
                    @click="goNext(item)"
                  >
                    mdi-arrow-right
                  </v-icon>
                </template>
              </v-data-table>
              <v-row>
                <v-spacer/>
                <v-btn
                  class="mb-2"
                  color="primary"
                  dark
                  @click="goBack()"
                >
                  {{ $t('返回') }}
                </v-btn>
              </v-row>
            </base-material-card>
          </v-container>
        </v-container>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import { getServantDetail, printServantSummaryBon } from '../../components/Utlis/api'
  import GlobalSettings from '../../components/Settings/GlobalSettings'
  import { IKUtils } from 'innerken-utils'
  import { formatDate } from '../../common/common'

  export default {
    props: {
      id: {
        default: -1,
        type: Number,
      },
    },
    data: function () {
      return {
        dates: [],
        menu: false,
        loading: false,
        currentIndex: 0,
        servantId: this.id,
        servantData: {},
        infoList: [],
        search: '',
        headers: [
          {
            text: 'Id',
            align: 'start',
            sortable: false,
            value: 'orderId',
          },
          {
            text: this.$i18n.t('tableName'),
            value: 'tableName',
          },
          {
            text: this.$i18n.t('time'),
            value: 'eatTime',
          },
          {
            text: this.$i18n.t('price'),
            value: 'totalPrice',
          },
          {
            text: this.$i18n.t('paymentStatus'),
            value: 'paymentStatus',
          },
          {
            text: this.$i18n.t('action'),
            value: 'action',
            sortable: false,
          },
        ],
        desserts: [],
      }
    },
    computed: {
      src: function () {
        if (this.servantData.basic) {
          return this.$store.getters.rootUrl + GlobalSettings.ServantImgRoot + this.servantData.basic.photo
        } else {
          return false
        }
      },
      defalutLogoUrl: function () {
        return 'this.src=' + require('@/assets/logonew.png')
      },
    },
    created () {
      this.loadDetail(this.servantId)
    },
    mounted () {
      this.dates = [formatDate(0), formatDate(0)]
      // this.loadDetail(this.servantId)
    },
    methods: {
      goNext (item) {
        this.$router.push({
          name: 'Seite mit den Rechnungsdetails',
          params: { id: item.orderId },
        })
      },
      clear () {
        this.dates = []
      },
      loadDetail (id) {
        getServantDetail(id).then(res => {
          this.servantData = res.content
          this.infoList.push({ text: this.$i18n.t('总单数') + ': ' + this.servantData.display.count })
          this.infoList.push({ text: this.$i18n.t('今日营业总额') + ': ' + this.servantData.display.totalPrice })
          this.infoList.push({ text: this.$i18n.t('现金') + ': ' + this.servantData.bar })
          this.infoList.push({ text: this.$i18n.t('EC') + ': ' + this.servantData.ec })
          this.infoList.push({ text: this.$i18n.t('tipIncome') + ': ' + this.servantData.display.tip })
          this.desserts = this.servantData.order
        })
      },
      printSummary (id) {
        if (this.dates[1] === undefined) {
          this.dates[1] = this.dates[0]
        }
        if (Date.parse(this.dates[0]) > Date.parse(this.dates[1])) {
          [this.dates[0], this.dates[1]] = [this.dates[1], this.dates[0]]
        }
        printServantSummaryBon(id, this.dates[0], this.dates[1]).then(() => {
          IKUtils.toast()
        })
      },
      goBack () {
        this.$router.history.go(-1)
        // this.$router.push({ name: '桌子管理' })
      },
    },
  }
</script>

